import 'axios';
import axios from 'axios';

const api = axios.create({
    baseURL: 'https://eq2reappapi.mooo.com'
});

async function postData(url, data) {
    try {
        await api.post(url, data);
    }
    catch (err) {
        console.error(err);
    }
}

export async function updatePlayer(server, name) {
    if (!server || !name) {
        return;
    }

    await postData('/players', {
        server: server,
        name: name
    });
}
