    &[src^="data"] {
      visibility: hidden;
    }

    &.cboc {
      border: 2px solid #c75a00;
      border-radius: 4px;
      background-color: #83300046;
    }

  }
}

ul.slots-purple {
  margin: 0 10px 0 12px;
  padding: 0;
  list-style: none;

  li {
    margin: -10px;
    padding: 0;

    a {
      display: inline-block;
      width: 10px;
      height: 10px;
      font-size: 0;
      white-space: nowrap;
      overflow: hidden;
      outline: none;
      box-sizing: border-box;
      border: 1px solid #650072;
      border-radius: 50%;
      background-color: #cf9bd6;
      cursor: default;
    }
  }
}

a {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.upgrade-link {
  margin-left: 0.3em;
  opacity: 0.75;
}

.mythical {
