  border: 1px solid rgb(161, 206, 252);
}
.badge-label {
  display: inline-block;
  min-width: 4ch;
  font-size: 0.9em;
}
.stats {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em 1.5em;
